import React, { useEffect, useState } from "react";
import moment from "moment-timezone";


const Achievements = () => {
  const [exp, setExp] = useState("-")
  let ports = require('../../portfolios.json');
  const [last, setLast] = useState("-");

  useEffect(() => {
    getExp()
    getLast()
  }, [])

  function getExp() {
    const birth = new Date(2017, 11, 1, 0, 0, 0)
    let now = new Date()
    now = moment(now).tz('Asia/Jakarta').format()
    const y = now.substr(0, 4)
    const m = now.substr(5, 2) * 1
    let myexp = y - birth.getFullYear()
    if (m - birth.getMonth() + 1 < 0) {
      myexp--;
    }
    setExp(myexp)
  }

  function getLast() {
    let prt = ports.sort((a, b) => b.tahunn - a.tahunn)
    setLast(prt[0]['tahunn'])
  }

  const achievementsContent = [
    { title: exp, subTitle1: "years of", subTitle2: "experience" },
    { title: ports.length, subTitle1: "completed", subTitle2: "projects" },
  ];

  return (
    <div className="row">
      <div className="col-12">
        <div className="box-stats with-margin">
          <h4 className="poppins-font position-relative">{last}</h4>
          <p className="open-sans-font m-0 position-relative text-uppercase">
            Last Project
          </p>
        </div>
      </div>
      {achievementsContent.map((val, i) => (
        <div className="col-6" key={i}>
          <div className="box-stats with-margin">
            <h3 className="poppins-font position-relative">{val.title}</h3>
            <p className="open-sans-font m-0 position-relative text-uppercase">
              {val.subTitle1} <span className="d-block">{val.subTitle2}</span>
            </p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Achievements;
