import React, { useState, useEffect } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import Modal from "react-modal";
import DirectionReveal from "direction-reveal";

const Portfolio = () => {
  const port = require('../../portfolios.json');
  const [ports, setPorts] = useState(null);
  const [isOpen, setIsOpen] = useState(false);
  const [selected, setSelected] = useState(null);
  const [jenis, setJenis] = useState([]);

  useEffect(() => {
    let jen = [];
    port.map((v) => {
      if (jen.indexOf(v.jenis.toLowerCase()) < 0) {
        jen.push(v.jenis.toLowerCase())
      }
    })
    setJenis(jen)

    let prt = port.sort((a, b) => b.tahunn - a.tahunn)
    setPorts(prt)
  }, [])

  function componentWillMount() {
    document.body.classList.add('modal-open')
  }

  function componentWillUnmount() {
    this.dismounted = true
    document.body.classList.remove('modal-open')
  }

  function toggleModal() {
    isOpen && setSelected(null)
    setIsOpen(!isOpen)
  }

  return (
    <Tabs>
      {ports != null && (<>
        <TabList className="portfolio-tab-list">
          <Tab>ALL</Tab>
          {
            jenis.map((v) => (<Tab key={v}>{v.toUpperCase()}</Tab>))
          }
        </TabList>

        <div className="portfolio-tab-content">
          <TabPanel>
            <ul className="row grid justify-content-center">

              {ports.map((v) => {
                return <li
                  data-aos="fade-right"
                  data-aos-duration="1200"
                  data-aos-delay="0"
                  key={v.id}
                >
                  <figure onClick={(e) => {
                    setSelected(v)
                    toggleModal()
                  }}>
                    <img src={v.gambar} alt={v.nama} />
                    <div className=" hover-content-wrapper">
                      <span className="content-title">{v.nama}</span>
                    </div>
                  </figure>

                </li>
              })}

            </ul>
          </TabPanel>

          {
            jenis.map((v) => (
              <TabPanel key={v}>
                <ul className="row grid justify-content-center">

                  {ports.filter(i => i.jenis.toLowerCase() == v).map((vv) => {
                    return <li
                      data-aos="fade-right"
                      data-aos-duration="1200"
                      data-aos-delay="0"
                      key={vv.id}
                    >
                      <figure onClick={(e) => {
                        setSelected(vv)
                        toggleModal()
                      }}>
                        <img src={vv.gambar} alt={vv.nama} />
                        <div className=" hover-content-wrapper">
                          <span className="content-title">{vv.nama}</span>
                        </div>
                      </figure>
                    </li>
                  })}

                </ul>
              </TabPanel>
            ))
          }
        </div>
      </>)}

      {selected != null && (
        <Modal
          isOpen={(selected != null && selected.id == selected.id) ? true : false}
          onRequestClose={toggleModal}
          contentLabel={selected.nama}
          className="custom-modal dark"
          overlayClassName="custom-overlay dark"
          closeTimeoutMS={500}
        >
          <div>
            <button className="close-modal" onClick={toggleModal}>
              <img src="/img/cancel.svg" alt="close icon" />
            </button>

            <div className="box_inner portfolio" style={{ overflowY: "auto", height: 500 }}>
              <div className="slideshow">
                <figure>
                  <figcaption>
                    <h3>{selected.nama}</h3>
                    <h6 style={{ textAlign: "center", marginTop: -30 }}>{selected.lingkup}</h6>
                    <p style={{ textAlign: "center", marginTop: -10 }}>Powered by : {selected.powered}</p>
                    {selected.copyright !== undefined && (<div className="text-center" style={{ marginBottom: 30 }}>
                      <a style={{ color: "white" }} target="_blank" href="https://pdki-indonesia.dgip.go.id/detail/EC00202012881?type=copyright">COPYRIGHT DETAIL</a>
                    </div>)}
                    <div className="row open-sans-font">
                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-file-text-o pr-2"></i>
                        <span className="project-label">Project </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.jenis}</span>
                      </div>

                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-building-o pr-2"></i>
                        <span className="project-label">Client </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.klien}</span>
                      </div>

                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-code pr-2"></i>
                        <span className="project-label">Stacks </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.teknologi}</span>
                      </div>

                      {/* <div className="col-12 col-sm-6 mb-2">
        <i className="fa fa-external-link pr-2"></i>
        <span className="project-label">Domain </span>:{" "}
        <span className="ft-wt-600 uppercase">
          <a
            href={selected.domain}
            target="_blank"
            rel="noreferrer"
          >
            {selected.domain}
          </a>
        </span>
      </div> */}

                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-calendar pr-2"></i>
                        <span className="project-label">Year </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.tahun}{selected.tahunn != selected.tahun ? " - " + selected.tahunn : ""}</span>
                      </div>

                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-users pr-2"></i>
                        <span className="project-label">Team </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.tim} Person</span>
                      </div>

                      <div className="col-12 col-sm-6 mb-2">
                        <i className="fa fa-male pr-2"></i>
                        <span className="project-label">Position </span>:{" "}
                        <span className="ft-wt-600 uppercase">{selected.posisi}</span>
                      </div>

                    </div>
                  </figcaption>

                  <img style={{ width: "100%" }} src={selected.gambar} alt={selected.nama} />
                </figure>
              </div>
            </div>
          </div>
        </Modal>)}
    </Tabs>
  );
};

export default Portfolio;
