import React, { useEffect, useState } from "react";
import moment from "moment-timezone";


const socmedItem = [
  { icon: "fa-github", menuName: "Github", link: "https://github.com/asbukhori" },
  { icon: "fa-facebook", menuName: "Facebook", link: "https://www.facebook.com/asbukhori" },
  { icon: "fa-instagram", menuName: "Instagram", link: "https://www.instagram.com/asbukhori" },
  { icon: "fa-linkedin", menuName: "Linkedin", link: "https://www.linkedin.com/in/asbukhori" },
  // { icon: "fa-comments", menuName: "Blog" },
];

const Hero = () => {
  const [exp, setExp] = useState(new Date().getFullYear() - 2017)

  useEffect(() => {
    getExp()
  }, [])

  function getExp() {
    const birth = new Date(2017, 11, 1, 0, 0, 0)
    let now = new Date()
    now = moment(now).tz('Asia/Jakarta').format()
    const y = now.substr(0, 4)
    const m = now.substr(5, 2) * 1
    let myexp = y - birth.getFullYear()
    if (m - birth.getMonth() + 1 < 0) {
      myexp--;
    }
    setExp(myexp)
  }

  const heroContent = {
    heroImage: "img/profile.png",
    heroMobileImage: "img/profile-mobile.png",
    heroTitleName: "Abdush Shomad Bukhori",
    heroDesignation: "Full Stack Software Engineer",
    heroDescriptions: `I am an IT professional with ` + exp + `+ years of experience in developing web or mobile based applications. Think critically in solving problems effectively and efficiently. Have good communication skills and love to discuss about the issues in application development.
    The scope of applications that I have developed include Hospitality, Healthcare, E-Commerce, Onlineshop, Company Profile, Games, E-Vote, Personal Blog and Fintech.
    The Featured Technology Stack that I use include Codeigniter Framework, Golang, ReactJS, Flutter, MySQL, PostgreSQL, Firebase.`,
  };

  return (
    <>
      <div className="row home-details-container align-items-center">
        <div
          className="col-lg-4 bg position-fixed d-none d-lg-block"
          style={{
            backgroundImage: `url(${heroContent.heroImage})`,
          }}
        ></div>
        <div className="col-12 col-lg-8 offset-lg-4 home-details  text-center text-lg-start">
          <div style={{ paddingBottom: 50, paddingTop: 50, marginBottom: 50 }}>
            <img
              src={`${heroContent.heroMobileImage}`}
              className="img-fluid main-img-mobile d-sm-block d-lg-none"
              alt="hero man"
            />
            <h2 className="text-uppercase poppins-font">
              I'm {heroContent.heroTitleName}.
              <span>{heroContent.heroDesignation}</span>
            </h2>
            {heroContent.heroDescriptions.split('\n').map((it, i) => (
              <p className="open-sans-font" style={{ textAlign: "justify" }} key={'x' + i}>{it}</p>
            ))}
            <div className="col-12 text-center">
              {socmedItem.map((item, i) => (
                <button className="button" style={{ margin: 5 }} key={i} onClick={() => {
                  window.location.href = item.link;
                }}>
                  <span className="button-text">{item.menuName}</span>
                  <span className={`button-icon fa ${item.icon}`}></span>
                </button>
              ))}
            </div>
          </div>
        </div>
      </div>
      {/* End home-details-container */}

    </>
  );
};

export default Hero;
